var _this;
export default {
	data() {
		return {
			
		}
	},
	mounted() {
		_this = this;
	},
	methods: {
		searchIcon(e) {
			let key = e.detail.value.toLowerCase();
			let list = this.cuIcon;
			for (let i = 0; i < list.length; i++) {
				let a = key;
				let b = list[i].name.toLowerCase();
				if (b.search(a) != -1) {
					list[i].isShow = true
				} else {
					list[i].isShow = false
				}
			}
			this.cuIcon = list
		}
	}
}
